<template>
  <div id="app">
    <input placeholder="username" v-model="username"><br/><br/>
    <input placeholder="password" v-model="password"><br/><br/>
    <button v-on:click="login">submit</button>
  </div>
</template>

<script>
import io from 'socket.io-client'
export default {
  name: 'app',
  data(){
    return{
      username:'',
      password:''
    }
  },
  created(){
    const that = this;
    this.socket = io.connect('http://192.168.3.104:8081');
    this.socket.on('news', function(obj){
      console.log(obj);
    })
  },
  methods:{
    login: function(){
      this.socket.emit('login', { uname: this.username, upwd: this.password });
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



